h1 {
  padding-top: 30px;
  text-align: center;
  background: -webkit-linear-gradient(#4c00ff, #8257e5);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 70px;
}
.img-logo{
  margin: auto;
  display: block;
}

#todo-list {
  width: 100%;
  max-width: 600px;
  margin: 60px auto;
  background: rgb(0, 0, 0);
  border-radius: 10px;
  box-shadow: 0px 0px 0px 8px rgba(0,0,0,0.3); 
}
form, .todo {
  max-width: 500px;
  margin: 60px auto;
  border-width: 2px;
  border-style: solid;
  border-image: linear-gradient(to right, #8257e5, #4c00ff) 1;
  padding: 10px 20px;
  margin-bottom: 10px;
  border-radius: 10px;
}
form, .todo:hover{
  transform: scale(1.02) translateY(1px);
}
form {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  border: none;
}
form input {
  padding: 4px 8px;
  flex-grow: 1;
  margin-right: 16px;
}
.todo {
  display: flex;
  flex-direction: column;
}

/*------- START BUTTON SECTION ---------*/
.update{
  background-image: linear-gradient(to right, #1FA2FF 0%, #12D8FA  51%, #1FA2FF  100%);
  margin: 10px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;            
  box-shadow: 0 0 2px #eee;
  border-radius: 10px;
}
.update:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
.create{
  background-image: linear-gradient(to right, #02AAB0 0%, #00CDAC  51%, #02AAB0  100%);
  margin: 10px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;            
  box-shadow: 0 0 2px #eee;
  border-radius: 10px;
}
.create:hover{
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
.delete{
  background-image: linear-gradient(to right, #FF512F 0%, #DD2476  51%, #FF512F  100%);
  margin: 10px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;            
  box-shadow: 0 0 2px #eee;
  border-radius: 10px;
}
.delete:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

button {
  font-size: 16px;
  border-radius: 8px;
  border: none;
  padding: 8px 16px;
  cursor: pointer;
}
button:focus {
  outline: 0;
}
/*------- END BUTTON SECTION ---------*/


input[type='text'] { font-size: 20px; font-family: monospace; border-radius: 10px;}
.todo input[type="checkbox"] {
  transform: scale(1.8) translateY(1px);
  margin: 0 16px 0 0;
  cursor: pointer;
}
.todo-text input {
  padding: 4px;
  border-radius: 10px;
}
.todo-text {
  font-size: 24px;
  line-height: 24px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  color:#fff;
}
.todo-completed {
  display: flex;
  margin: 10px 0;
}
.todo-actions {
  display: flex;
  justify-content: space-between;
}

/*------ MEDIA QUERYS PARA DISPOSITIVOS MOBILE ------*/

@media only screen and (max-width: 416px) {
  h1 {
    font-size: 50px;
  }
  input[type='text'] { font-size: 16px;}
  .todo input[type="checkbox"] {
  cursor: pointer;
  }
  form input {
    margin-right: 0px;
  }
  
}